import React from 'react'
import styled from 'styled-components'

const MenuCont = styled.div`
    position: relative;
    border: 2px solid red;
    height: 100%;
    width: 50%;
    text-align: center;
    margin: 0 auto;
    top: 10vh;
    font-family: "fjalla one";
    text-transform: uppercase;
    background-color: rgb(240, 237, 232);
`;

const MenuHead = styled.div`
    h1 {
        margin: 0 auto;
        padding: 0;
        font-size: 4vw;
        color: rgb(0, 0, 0);
    }
    p {
        width: 40%;
        margin: 0 auto;
        text-align: center;       
        font-size: 1vw;
    }
`;

const MenuItem = styled.div`
    margin-top: 8vh;
    padding: 0;
    font-size: 1.5vw;
`;

const MenuDesc = styled.div`
    padding: 0;
    margin-top: 2vh;  
    margin-bottom: 5vh;
    font-size: 1.1vw;   
    text-transform: none;
`;

const MenuPage = () => (
    <div>
        <MenuCont>
            <MenuHead>
                <h1>Menu</h1>
                <p>Kitchen closes at 3pm</p>
            </MenuHead>       
                
            <MenuItem>
                Croissant Sait Jerome
            </MenuItem>
            <MenuDesc>
                crab and salmon omlette . salmoe roe . confit tomatoes . fine herbs . french croissant .
            </MenuDesc>

            <MenuItem>
                Eggs Bambino
            </MenuItem>
            <MenuDesc>
                smoked salmon croquette . poached eggs . citrus cured king salmon . salmon caviar . broccolini . maltaise sauce .
            </MenuDesc>

            <MenuItem>
                Yakuza Burger
            </MenuItem>
            <MenuDesc>
                grilled chicken . mozzarella katsu . Green's secret sauce . cos lettuce . pickles . milk buns . seasoned chips .
            </MenuDesc>

            <MenuItem>
                Lost Bread
            </MenuItem>
            <MenuDesc>
                brioche french toast . wild strawberry ice cream . vanilla marscapone chantilly . ricotta . strawberry curd . gold leaf . fresh and freeze dried strawberry .
            </MenuDesc>

            <MenuItem>
                GMC
            </MenuItem>
            <MenuDesc>
                Green's matcha cake . assorted textures of matcha cookie . mousse . sponge . tempered chocolate . ganache .
            </MenuDesc>

            <MenuItem>
                Breakfast with the Green's
            </MenuItem>
            <MenuDesc>
                miso grilled king salmon . poached egg . radish petit salad . kewpi mayonnaise . furikake .
            </MenuDesc>
        </MenuCont>
    </div>
)

export default MenuPage


